'use client'

import React from 'react'

const Illustration = ({ imageSrc, title, subTitle }) => {
    return (
        <div className="hidden md:flex w-3/5 flex-col justify-center px-16">

            {/* TEXT ABOVE ILLUSTRATION */}
            <div className="mb-10">
                <h2 className="text-4xl font-bold text-secondary leading-tight">
                    {title}
                </h2>

                {subTitle && <p className="text-gray-600 text-lg mt-4">
                    {subTitle}
                </p>}
            </div>

            {/* ILLUSTRATION */}
            <img
                src={imageSrc}
                alt="Illustration"
                className="w-4/5 max-w-xl drop-shadow-md"
            />
        </div>
    )
}

export default Illustration